.footer {
  display: flex;
  flex-direction: column;
  align-items: center;

  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.02em;
  color: $footer-text-color;
  background-color: $footer-bg-color;
}
// .footer__container {
//   width: 100%;
//   margin-left: auto;
//   margin-right: auto;
//   padding-left: 20px;
//   padding-right: 20px;
// }
.footer__icon {
  width: 14px;
  height: 12px;
  margin-left: 10px;
  margin-right: 10px;
  fill: $accent-color;
}
.footer__name {
  margin-bottom: 10px;
  padding-top: 29px;
}
.footer__text {
  margin: 0;
  padding-bottom: 29px;
}
.footer__link {
  text-decoration: underline;
  color: $footer-text-color;
}
// @media screen and (min-width: 320px) {
//   .footer__container {
//     width: 280px;
//   }
// }
@media screen and (min-width: 768px) {
  .footer {
    flex-direction: row;
    justify-content: center;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 16px;
    // width: 618px;
  }
  .footer__name {
    margin-bottom: 0;
    padding-bottom: 29px;
  }
  .footer__text {
    padding-top: 29px;
    margin-left: 5px;
  }
}
@media screen and (min-width: 1200px) {
  .footer__container {
    width: 882px;
  }
}
